body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-Header
{
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Data
{
  display: flex;
  flex-wrap: wrap;
}

.Details
{
  cursor: pointer;
  border-radius: 4px;
  padding: 8px;
  background-color: #232f34;
  margin: 1rem;
  width: 320px;
  color: #fff;

  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);

}

.Details img
{
  max-width: 100%;
}

.overview, h2, h3, p, span
{
  margin: 2px;
  padding: 0px;
  background-color: #232f34;
}

h2
{
  font-weight: 700;
}

h3
{
  font-size: medium;
  font-weight: normal;
}

span
{
  /* border: 2px solid #fff; */
  height: fit-content;
  background-color: #4A6572;
  padding: 0.5rem;
  border-radius: 5px;
  font-weight: bold;
}

.heading
{
  background-color: #232f34;
  color: #fff;
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  padding: 8px;
}
